import React, { Component } from 'react';
import { Panel,Button } from 'tinper-bee';
import PropTypes from 'prop-types';
import './index.less';
import classnames from 'classnames';
/**
 * this.props.form.resetFields()resetreset
 */
const propTypes = {
    searchOpen:PropTypes.bool,//false
    search: PropTypes.func,//
    reset:PropTypes.func,//
    resetName:PropTypes.string,//
    searchName:PropTypes.string,//
    title: PropTypes.string
};

const defaultProps = {
    searchOpen:false,
    search: () => {},
    reset: () => {},
    title: ""
};


class SearchPanel extends Component {
    constructor(props) {
        super(props);
        this.state = {
            searchOpen:this.props.searchOpen
        };
    }
    componentDidMount() {

    }

    open=()=>{
        this.setState({
            searchOpen: !this.state.searchOpen
        })
    }

    search=()=>{
        let self=this;
        this.props.form.validateFields((err, values) => {
            self.props.search(err, values);
        });
    }
    reset=()=>{
        this.props.form.resetFields();
        this.props.reset();
    }
    render() {
        const {children,className,form,resetName,searchName } = this.props;
        let classes = 'search-panel';
        if(className){
            classes += ' '+className
        }
        let header = () => {
            return (
                <div className="clearfix" onClick={this.open}>
                    <span  className={'search-panel-title'}>
                        {this.props.title}
                    </span>
                    <span  className={'search-panel-icon'}>
                        {this.state.searchOpen ? '' : ''}
                        <i className={classnames({
                                'uf': true,
                                'uf-arrow-down': this.state.searchOpen,
                                'uf-arrow-right': !this.state.searchOpen
                            })}/>
                    </span>
                </div>
            )
        };
        return (
           <Panel className={classes}  header={header()}  collapsible expanded={this.state.searchOpen}>
                {children}
                <div className='search-panel-btn'>
                    <Button size='sm' className='reset-btn' onClick={this.reset}>{resetName?resetName:''}</Button>
                    <Button size='sm' className='submit-btn' onClick={this.search}>{searchName?searchName:''}</Button>
                </div>
           </Panel>
        )
    }
}
SearchPanel.propTypes = propTypes;
SearchPanel.defaultProps = defaultProps;
export default SearchPanel;



