//
import React from 'react'
import { Route } from 'mirrorx'

//
import {
    ConnectedNewYear
} from '../../container'

/**
* 
* 1search-table
* simple-tableform+
* pagination-tableform+
* 6ref-exmaple
* 7master-table
*/
export default ({ match }) => (
    <div className="templates-route">
    {/**/}
    {<Route exact path={'/'} component={ConnectedNewYear} />}
    {/**/}
    <Route exact path={`${match.url}/happynewyear`} component={ConnectedNewYear} />
    </div>
    )


